.TodoInput {
    background: #333333;
    border-radius: 4px;
    border: 2px solid #202329;
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: #FFF;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoInput::placeholder {
    color: #FFF;
    font-family: 'Montserrat';
    font-weight: 400;
}

.TodoInput:focus {
    outline-color: #61DAFA;
}