form {
    width: 90%;
    max-width: 300px;
    /* background-color: hsl(0, 0%, 15%); */
    background-color: rgba(17, 17, 17, 1);
    padding: 33px 40px;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

label {
    text-align: center;
    font-weight: 700;
    font-size: 20px;
    color: #FFF;
    margin-bottom: 26px;
}

textarea {
    background-color: #333333;
    border: 2px solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, .25);
    color: #FFF;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% -25px);
}
textarea::placeholder {
    color: #FFF;
    font-family: 'Montserrat';
    font-weight: 400;
}
textarea:focus {
    outline-color: #61DAFA;
}

.todoForm-buttonsContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.todoFrom-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 4px;
    border: none;
    font-family: 'Montserrat';
}

.todoFrom-button--cancel {
    background: transparent;
    border: 1px solid #61DAFA;
    color: #61DAFA;
}

.todoFrom-button--add {
    background: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    color: #4D4D4D;
}