.TodoItem {
    /* background-color: hsl(0, 0%, 20%); */
    background-color: rgba(17, 17, 17, 1);
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 55px;
    margin-top: 24px;
    padding: 12px 75px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
    border-radius: 10px;
}

.TodoItem-text {
    /* margin: 24px 0px 24px 24px; */
    margin: 0px 16px;
    width: 100%;
    max-width: calc(100% -120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    text-align: center;
    overflow-wrap: break-word;
}

.TodoItem-text--completed {
    text-decoration: line-through;
}